<template>
  <div class="news">
    <mt-header title="新闻公告" id="header">
      <router-link to="/" slot="left" >
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div  id="wrap">
      <div class="wrap" v-for="val in arr">
          <h2 v-text="val.title">习近平访美</h2>
          <div id="wrp1">
            <p id="neirong" v-text="val.contentText+'......'">在中国，国家主席访美前夕</p>
          </div>

          <span v-text="new Date(val.createTime).getFullYear()+'年'+(new Date(val.createTime).getMonth()+1)+'月'+new Date(val.createTime).getDate()+'日'">2015年09月11日 15：50</span>
          <router-link :to="'/detailnews?id='+val._id" id="detail">查看详情</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "news",
        data () {
          return{
            arr: [ ],
            mon :''
          }
        },
        methods:{
            getNews(){
              let that = this
              axios.get('/api/news/get')
                .then(function (response) {
                  that.arr = response.data.data
                  console.log(response.data.data);
                })
                .catch(function (error) {
                  console.log(error);
                });
            }
        },
      mounted() {
        this.getNews()
      }
    }
</script>
<style>
  #wrap img{
    display: block;
    width: 320px;
    height: 200px;
  }
</style>

<style scoped>
  #neirong{
    float: left;
  }
  #wrp1{
    width: 320px;
    height:52px;
    overflow: hidden;
  }
  #wrap{
    margin-bottom: 45px;
  }

  .news #header{
    height: 45px;
    background: #006ed8;
  }
  .news img{
    width: 100%;
    height: 151px;
    display: block;
  }
  .news .dengji{
    width: 100%;
    display: block;
    height: 440px;
  }
  .wrap{

    width: 340px;
    height: 120px;
    margin: 0 auto;
    margin-top: 50px;
    background: #fff;
    border: 1px solid #ccc;
    border-left: 8px solid #006eb8;
    position: relative;
  }
  .wrap h2{
    font-size: 20px;
    font-weight: 700;
    margin: 5px;
  }
  .wrap p{
    margin: 5px;
    color: #b8b8b8;
  }
  .wrap span{
    color: #b8b8b8;
    float: right;
    margin-top: 18px;
  }
  .wrap a{
    text-decoration: none;
    color: #fff;
    display: block;
    background: #7a7a7a;
    width: 95px;
    height: 34px;
    text-align: center;
    line-height:34px ;
  }
  #detail{
    position: absolute;
    right: 0px;
    bottom: -35px;
  }
</style>



